/**
 * Highlights the currently evaluated section.
 *
 * @module     block_course_audit/highlight
 * @copyright  2025 Bastian Schmidt-Kuhl <bastian.schmidt-kuhl@ruhr-uni-bochum.de>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

// block/course_audit/amd/src/highlight.js
define(['jquery'], function($) {
    return {
        highlightSection: function(sectionNumber) {
            const targetSection = $('#section-'+sectionNumber);
            if (!targetSection.length) { return; }

            // Scrollen mit Offset unter der Navbar
            $('html, body').animate({
                scrollTop: targetSection.offset().top - 100
            }, 800);

            // Highlight-Effekt
            targetSection.addClass('block-course-converter-highlight')
                .css('position', 'relative');

            // Pfeil-Indikator
            const arrow = $('<div class="block-course-converter-arrow"></div>');
            $('body').append(arrow);
            // Positionierung
            const updateArrowPosition = () => {
                const rect = targetSection[0].getBoundingClientRect();
                arrow.css({
                    left: rect.left - 40,
                    top: rect.top + (rect.height/2)
                });
            };
            updateArrowPosition();
            // Cleanup nach 5s
            setTimeout(() => {
                targetSection.removeClass('block-course-converter-highlight');
                arrow.remove();
            }, 5000);
        }
    };
});